.Bar {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin-bottom: 10px;
  border: 1px solid #aaa;
  height: 70px;
  font-size: 20px;
}

.Bar:focus {
  /* for accessibility, add an outline on focus */
  outline: 1px solid #ffc107;
}

.Bar--active {
  border-color: #555;
  -webkit-box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
          box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
}

/* Note that the width is defined as a style in the component */
.Bar__fill {
  position: absolute;
  height: 100%;
  left: 0;
  top: 0;
  background: #90caf9;
  z-index: -1;
  -webkit-transition: width 200ms;
  -o-transition: width 200ms;
  transition: width 200ms;
}

.Bar__fill--over-warning {
  background: #ef5350;
}



